body {
  min-height: 100vh;
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 24px;
  font-family: "Helvetica", "Arial", sans-serif;
  color: #000000;
  text-rendering: optimizeLegibility;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

a {
  color: #000000;
  text-decoration: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

.header,
.footer {
  flex-shrink: 0;
}

main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

@font-face {
  font-family: "Helvetica";
  src: url("../fonts/helvetica-regular.woff2") format("woff2"), url("../fonts/helvetica-regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Helvetica";
  src: url("../fonts/helvetica-bold.woff2") format("woff2"), url("../fonts/helvetica-bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
.container {
  width: 100%;
  max-width: 1600px;
  margin: 0 auto;
  padding: 0 200px;
}
@media (max-width: 1100px) {
  .container {
    padding: 0 150px;
  }
}
@media (max-width: 1023px) {
  .container {
    padding: 0 40px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 16px;
  }
}

.container--goods {
  padding: 0 200px 0 100px;
}
@media (max-width: 1023px) {
  .container--goods {
    padding: 0 40px;
  }
}
@media (max-width: 767px) {
  .container--goods {
    padding: 0 16px;
  }
}

@media (max-width: 767px) {
  .container--header {
    background-color: #ffffff;
  }
}

.header {
  border-bottom: 1px solid #c8c8c8;
  background-color: #ffffff;
}
@media (max-width: 767px) {
  .header {
    border-bottom: none;
    position: relative;
    z-index: 2;
  }
}
.header__wrapper {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 25px 0 22px;
}
@media (max-width: 767px) {
  .header__wrapper {
    z-index: 2;
  }
}
.header__logo {
  margin: 0 0 3px 0;
}
@media (max-width: 767px) {
  .header__nav {
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
    transform: translateY(-100%);
    transition: transform 0.8s ease-in-out, height 0.8s ease-in-out, opacity 0.8s ease-in-out;
    z-index: -1;
  }
}
.header__tools {
  margin: 0 0 0 15px;
  padding: 0;
}
@media (max-width: 767px) {
  .header__tools {
    margin: 0 24px 0 auto;
  }
}

.navigation {
  max-width: 280px;
  margin: 0 15px 0 0;
  width: 100%;
}
.navigation__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}
@media (max-width: 767px) {
  .navigation__list {
    width: 100%;
    flex-direction: column;
    align-items: flex-start;
  }
}
.navigation__item:not(:last-child) {
  margin: 0 40px 3px 0;
}
@media (max-width: 767px) {
  .navigation__item:not(:last-child) {
    margin: 0;
  }
}
.navigation__item {
  margin: 0 0 3px 0;
}
@media (max-width: 767px) {
  .navigation__item {
    width: 100%;
  }
}
.navigation__link {
  display: block;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  color: #000000;
}
@media (max-width: 767px) {
  .navigation__link {
    width: 100%;
    font-size: 18px;
    line-height: 22px;
    padding: 15px 0;
  }
}
.navigation__link:hover {
  text-decoration: underline;
}
@media (max-width: 767px) {
  .navigation__link:hover {
    text-decoration: none;
    background-color: #f1f1f1;
  }
}
@media (max-width: 767px) {
  .navigation__link:active {
    font-weight: 600;
    background-color: transparent;
  }
}
.navigation__link--active {
  text-decoration: underline;
}
@media (max-width: 767px) {
  .navigation {
    margin: 0;
    max-width: 100%;
    width: 100%;
    padding: 0 16px 25px;
  }
}

.tools {
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  max-width: 170px;
}

.tools__item:not(:last-child) {
  margin: 0 30px 3px 0;
}

.tools__item {
  position: relative;
  margin: 0 0 3px 0;
}
.tools__item span {
  position: absolute;
  top: -6px;
  right: -8px;
  width: 15px;
  height: 15px;
  border-radius: 50%;
  background-color: #000000;
  color: #ffffff;
  font-weight: 400;
  font-size: 10px;
  line-height: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
}
@media (max-width: 767px) {
  .tools__item {
    display: none;
  }
}

@media (max-width: 767px) {
  .tools__item--bag span {
    display: none;
  }
}
@media (max-width: 767px) {
  .tools__item--bag {
    display: block;
  }
}

.tools__link {
  transition: color 0.3s ease;
}
.tools__link svg {
  fill: none;
  width: 20px;
  height: 20px;
}
@media (max-width: 767px) {
  .tools__link svg {
    width: 28px;
    height: 28px;
  }
}

.tools__link:hover {
  color: #6e6e6e;
  transition: color 0.3s ease;
}

.burger-button {
  cursor: pointer;
  display: none;
  border: none;
  background-color: transparent;
  padding: 0;
  margin: 0;
  width: 32px;
  height: 32px;
  transition: color 0.3s ease;
}
.burger-button svg {
  width: 32px;
  height: 32px;
}
@media (max-width: 767px) {
  .burger-button {
    display: block;
  }
}

.burger-button:hover {
  color: #6e6e6e;
  transition: color 0.3s ease;
}

.opened {
  height: 100vh;
  opacity: 1;
  background-color: #ffffff;
  transform: translateY(11%);
  transition: transform 0.8s ease-in-out, height 0.8s ease-in-out, opacity 0.8s ease-in-out;
}