.container {
  width: 100%;
  max-width: 1600px;
  margin: 0 auto;
  padding: 0 200px;

  @media (max-width: 1100px) {
    padding: 0 150px;
  }

  @include tablet {
    padding: 0 40px;
  }

  @include mobile {
    padding: 0 16px;
  }
}

.container--goods {
  padding: 0 200px 0 100px;

  @include tablet {
    padding: 0 40px;
  }

  @include mobile {
    padding: 0 16px;
  }
}

.container--header {
  @include mobile {
    background-color: $color-white;
  }
}
